*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  .Login_body{
    justify-content: center;
    display: flex;
    align-items: center;
    min-height: 100vh;
    background: url("../imgaes//login-bg.jpg")no-repeat;
    background-size: cover;
    background-position: center;
  
    
  }
  
  .wrapper{
    border-radius: 20px;
    width: 420px;
    background: transparent;
    color: #fff;
    padding: 30px 40px;
    backdrop-filter: blur(30px);
    box-shadow: 0 0 10px rgba(0,0,0, .2);
  }
  .wrapper h1{
    font-size: 36px;
    text-align: center;
  }
  
  .wrapper .input-box{
    position: relative;
    width: 100%;
    height: 50px;
    margin: 30px 0;
  }
  
  .input-box input{
    width: 100%;
    height: 100%;
    background:transparent;
    border: none;
    outline: none;
    border: 2px solid rgba(255, 255, 255, 2);
    border-radius: 40px;
    color: #fff;
    padding: 20px 45px 20px 20px;
  }
  
  .input-box input::placeholder{
    color: #fff;
  }
  
  
  .input-box .icon{
    position: absolute;
    right: 20px;
    top: 20px;
    transform: translateY(-50%);
    font-size: 18px;
  }
  
  .wrapper button{
    width: 100%;
    height: 45px;
    background: #fff;
    border: none;
    outline: none;
    border-radius: 40px;
    box-shadow: 0 0 10px rgba( 0, 0, 0,.1);
    cursor: pointer;
    font-size: 16px;
    color: #333;
    font-weight: 700;
  }
  .box2{
    background:transparent;
  }